'use client';

import { Calendar, User, CreditCard, FileText } from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Separator } from '@/components/ui/separator';
import { Pembelian } from '@/types';

interface DetailPembelianInfoProps {
    data: Pembelian;
    formatDate: (val: string) => string;
}

export default function DetailPembelianInfo({ data, formatDate }: DetailPembelianInfoProps) {
    return (
        <div className="space-y-6">
            <Card>
                <CardHeader className="pb-3">
                    <CardTitle className="text-sm text-gray-500 uppercase tracking-wider">Supplier</CardTitle>
                </CardHeader>
                <CardContent>
                    <div className="font-bold text-lg">{data.supplier?.nama_supplier}</div>
                    <div className="text-sm text-gray-600 mt-1">{data.supplier?.alamat || '-'}</div>
                    <div className="text-sm text-gray-600 mt-1">Telp: {data.supplier?.nomor_telepon}</div>
                    <div className="text-sm text-gray-600">CP: {data.supplier?.kontak_person}</div>
                </CardContent>
            </Card>

            <Card>
                <CardHeader className="pb-3">
                    <CardTitle className="text-sm text-gray-500 uppercase tracking-wider">Info Transaksi</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                    <div className="flex items-center gap-3">
                        <div className="w-8 h-8 rounded-full bg-blue-50 flex items-center justify-center text-blue-600">
                            <Calendar className="w-4 h-4" />
                        </div>
                        <div>
                            <p className="text-xs text-gray-500">Tanggal Pembelian</p>
                            <p className="font-medium text-sm">{formatDate(data.tanggal_pembelian)}</p>
                        </div>
                    </div>
                    
                    <div className="flex items-center gap-3">
                        <div className="w-8 h-8 rounded-full bg-purple-50 flex items-center justify-center text-purple-600">
                            <FileText className="w-4 h-4" />
                        </div>
                        <div>
                            <p className="text-xs text-gray-500">No. Faktur Supplier</p>
                            <p className="font-medium text-sm">{data.no_faktur_supplier || '-'}</p>
                        </div>
                    </div>

                    <Separator />

                    <div className="flex items-center gap-3">
                        <div className="w-8 h-8 rounded-full bg-green-50 flex items-center justify-center text-green-600">
                            <CreditCard className="w-4 h-4" />
                        </div>
                        <div>
                            <p className="text-xs text-gray-500">Metode Pembayaran</p>
                            <p className="font-medium text-sm">{data.payment_method?.nama_metode}</p>
                        </div>
                    </div>

                    <div className="flex items-center gap-3">
                        <div className="w-8 h-8 rounded-full bg-orange-50 flex items-center justify-center text-orange-600">
                            <User className="w-4 h-4" />
                        </div>
                        <div>
                            <p className="text-xs text-gray-500">Dicatat Oleh (Admin)</p>
                            <p className="font-medium text-sm">{data.user?.name}</p>
                        </div>
                    </div>
                </CardContent>
            </Card>

            {data.catatan && (
                <Card>
                    <CardHeader className="pb-3">
                        <CardTitle className="text-sm text-gray-500 uppercase tracking-wider">Catatan</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <p className="text-sm text-gray-600 italic">"{data.catatan}"</p>
                    </CardContent>
                </Card>
            )}
        </div>
    );
}