'use client';

import { Store } from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import {
    Table, TableBody, TableCell, TableHead, TableHeader, TableRow,
} from '@/components/ui/table';
import { Pembelian } from '@/types';

interface DetailPembelianItemsProps {
    data: Pembelian;
    formatRupiah: (val: number | string) => string;
}

export default function DetailPembelianItems({ data, formatRupiah }: DetailPembelianItemsProps) {
    return (
        <Card className="md:col-span-2 h-fit">
            <CardHeader className="bg-gray-50 border-b">
                <CardTitle className="text-base flex items-center gap-2">
                    <Store className="w-4 h-4" /> Rincian Barang
                </CardTitle>
            </CardHeader>
            <CardContent className="p-0">
                <Table>
                    <TableHeader>
                        <TableRow>
                            <TableHead>Barang</TableHead>
                            <TableHead className="text-right">Jml</TableHead>
                            <TableHead className="text-right">Harga Satuan</TableHead>
                            <TableHead className="text-right">Total</TableHead>
                        </TableRow>
                    </TableHeader>
                    <TableBody>
                        {data.details?.map((item: any) => (
                            <TableRow key={item.id}>
                                <TableCell>
                                    <div className="font-medium">{item.bahan_baku?.nama_bahan_baku}</div>
                                    <div className="text-xs text-gray-500">{item.bahan_baku?.kategori?.nama_kategori}</div>
                                </TableCell>
                                <TableCell className="text-right">
                                    {Number(item.jumlah)} {item.bahan_baku?.satuan?.nama_satuan}
                                </TableCell>
                                <TableCell className="text-right font-mono text-gray-600">
                                    {formatRupiah(item.harga_beli_satuan)}
                                </TableCell>
                                <TableCell className="text-right font-bold text-gray-900">
                                    {formatRupiah(item.subtotal)}
                                </TableCell>
                            </TableRow>
                        ))}
                        {/* Baris Total */}
                        <TableRow className="bg-gray-50 font-bold">
                            <TableCell colSpan={3} className="text-right">Total Faktur</TableCell>
                            <TableCell className="text-right text-lg text-[#D93F21]">
                                {formatRupiah(data.total_harga_faktur)}
                            </TableCell>
                        </TableRow>
                    </TableBody>
                </Table>
            </CardContent>
        </Card>
    );
}