'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { useParams, useRouter } from 'next/navigation';
import { toast } from 'sonner';
import { Pembelian } from '@/types';

export function useDetailPembelianPage() {
    const params = useParams();
    const router = useRouter();
    const id = params.id;

    const [data, setData] = useState<Pembelian | null>(null);
    const [loading, setLoading] = useState(true);

    // 1. Fetch Data Detail
    const fetchDetail = async () => {
        try {
            const response = await axios.get(`/api/pembelians/${id}`);
            setData(response.data);
        } catch (error) {
            console.error(error);
            toast.error("Gagal memuat detail pembelian.");
            router.push('/admin/inventaris/pembelian');
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        if (id) fetchDetail();
    }, [id]);

    // 2. Formatters
    const formatRupiah = (num: number | string) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(Number(num));
    
    const formatDate = (dateString: string) => 
        new Date(dateString).toLocaleDateString('id-ID', { weekday: 'long', day: 'numeric', month: 'long', year: 'numeric' });

    // 3. Handle Void (Batalkan Transaksi)
    const handleVoid = async () => {
        if(!confirm("Yakin batalkan transaksi ini? Stok akan dikurangi otomatis.")) return;
        try {
            await axios.delete(`/api/pembelians/${id}`);
            toast.success("Pembelian dibatalkan.");
            fetchDetail(); // Refresh data untuk update status badge
        } catch (error: any) {
            toast.error(error.response?.data?.message || "Gagal membatalkan.");
        }
    };

    const handlePrint = () => {
        window.print();
    };

    return {
        data,
        loading,
        handleVoid,
        handlePrint,
        formatRupiah,
        formatDate
    };
}