'use client';

import { useDetailPembelianPage } from '@/hooks/admin/useDetailPembelianPage';

// Import Komponen UI
import DetailPembelianHeader from '@/components/admin/pembelian/DetailPembelianHeader';
import DetailPembelianItems from '@/components/admin/pembelian/DetailPembelianItems';
import DetailPembelianInfo from '@/components/admin/pembelian/DetailPembelianInfo';

export default function DetailPembelianPage() {
    const { 
        data, 
        loading, 
        handleVoid, 
        handlePrint, 
        formatRupiah, 
        formatDate 
    } = useDetailPembelianPage();

    if (loading) return <div className="p-8 text-center text-gray-500">Memuat detail...</div>;
    if (!data) return <div className="p-8 text-center text-red-500">Data tidak ditemukan.</div>;

    return (
        <div className="space-y-6 max-w-5xl mx-auto pb-20">
            {/* 1. Header & Actions */}
            <DetailPembelianHeader 
                data={data} 
                onPrint={handlePrint} 
                onVoid={handleVoid} 
            />

            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                
                {/* 2. Tabel Rincian Barang (Kiri - Lebar) */}
                <DetailPembelianItems 
                    data={data} 
                    formatRupiah={formatRupiah} 
                />

                {/* 3. Info Tambahan (Kanan - Sempit) */}
                <DetailPembelianInfo 
                    data={data} 
                    formatDate={formatDate} 
                />
            </div>
        </div>
    );
}