'use client';

import Link from 'next/link';
import { ArrowLeft, Printer, Trash2 } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Pembelian } from '@/types';

interface DetailPembelianHeaderProps {
    data: Pembelian;
    onPrint: () => void;
    onVoid: () => void;
}

export default function DetailPembelianHeader({ data, onPrint, onVoid }: DetailPembelianHeaderProps) {
    return (
        <div className="flex items-center justify-between print:hidden">
            <div className="flex items-center gap-4">
                <Link href="/admin/inventaris/pembelian">
                    <Button variant="outline" size="icon">
                        <ArrowLeft className="h-4 w-4" />
                    </Button>
                </Link>
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Detail Pembelian</h1>
                    <div className="flex items-center gap-2 text-sm text-gray-500">
                        <span className="font-mono">{data.kode_pembelian}</span>
                        <span>â€¢</span>
                        {data.status === 'selesai' ? (
                            <Badge className="bg-green-100 text-green-800 hover:bg-green-100 border-green-200">Selesai</Badge>
                        ) : (
                            <Badge className="destructive text-white bg-red-500">Dibatalkan</Badge>
                        )}
                    </div>
                </div>
            </div>
            <div className="flex gap-2">
                <Button variant="outline" onClick={onPrint}>
                    <Printer className="w-4 h-4 mr-2" /> Cetak
                </Button>
                {data.status === 'selesai' && (
                    <Button variant="destructive" onClick={onVoid}>
                        <Trash2 className="w-4 h-4 mr-2" /> Batalkan
                    </Button>
                )}
            </div>
        </div>
    );
}